import Header from "../ComponentChilds/Header";
import AboutHero from "../ComponentChilds/About/AboutHero";
import { lazy } from "react";
import { Helmet } from "react-helmet";
const AlmaxsportStats = lazy(() =>
  import("../ComponentChilds/About/AboutStats")
);
const AboutMission = lazy(() =>
  import("../ComponentChilds/About/AboutMission")
);
const Footer = lazy(() => import("../ComponentChilds/Footer"));

export default function AboutUsPage({ basketItemCount }) {
  return (
    <>
      <Helmet>
        <title>آلمکس اسپورت | درباره ی ما</title>
        {/* ✅ Schema Markup */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "Organization",
            name: "پروفیت",
            url: "https://allmaxsport.com",
            logo: "https://allmaxsport.com/logo.png",
            contactPoint: {
              "@type": "ContactPoint",
              telephone: "09392566494",
              contactType: "Customer Service",
              areaServed: "IR",
            },
            sameAs: [
              "https://instagram.com/allmaxsport",
              "https://t.me/allmaxsportsupport",
              "https://wa.me/09392566494",
            ],
          })}
        </script>
      </Helmet>
      <Header basketItemCount={basketItemCount} />
      <main className="font-vazir text-right bg-white">
        <AboutHero
          heroImage={
            "https://allmaxsport.com/Media/images/bumzyeixas1kkasidla8.avif"
          }
        />
        <AlmaxsportStats />
        <AboutMission />
      </main>
      <Footer />
    </>
  );
}
